import React from 'react'
import '../components/paragraphimageright.css'



class ParagraphImageRight extends React.Component{
constructor(props){
    super(props)
}



    render(props){
        const index = this.props
        return(
            <div className="paragraphWrapper0">
                <h1 className="title0">{this.props.data.data[this.props.id].title}</h1>
                <p>{this.props.data.data[this.props.id].content}</p>
                <img className="image" src={this.props.data.data[0].image}/>
            </div>
        )
    }
}

export default ParagraphImageRight